<template>
  <div id="ipcontainer" :style="{ width: '100%', height: '100vh' }">
    <div class="iptitle">
      <span class="content">重点影响力博主</span>
    </div>
    <div class="echart" id="mychart" :style="myChartStyle"></div>
  </div>

  <!-- :style="myChartStyle" -->
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      myChart: {},
      ydata:[
            "博主1",
            "博主2",
            "博主3",
            "博主4",
            "博主5",
            "博主6",
            "博主7",
            "博主8",
            "博主9",
            "博主10",
          ],
      sortData1: [], //动态排序数据
      sortData2: [], //动态排序数据
      myChartStyle: { width: "100%", height: "90vh" }, //图表样式
      dynamicSortZZTOption: {
        legend: {
          show: true,
          top: "3px",
          left: "30%",
          textStyle: {
            color: "#fff",
          },
        },
        grid: {
          left: "1%",
          right: "1%",
          bottom: "1%",
          top: "10%",
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          backgroundColor: "rgba(26, 26, 26, 0.4)",
          borderWidth: 0,
          textStyle: {
            color: "white",
          },
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow", // 默认为直线，可选为：'line' | 'shadow'
          },

          formatter: function (params) {
            var res =
              "<div><p style='font-size:14px'>" + params[0].name + "</p></div>";
            res +=
              "<p>" +
              params[0].seriesName +
              ":" +
              params[0].data +
              "</p>" +
              "<p>" +
              params[1].seriesName +
              ":" +
              params[1].data +
              "</p>";

            return res;
          },
        },

        xAxis: {
          max: "dataMax",
          axisLine: {
            show: false,
          },
          axisLabel: {
            show: false,
          },
          splitLine: {
            show: false,
          },
        },
        yAxis: {
          type: "category",
          axisLine: {
            show: false,
            lineStyle: {
              color: "#fff",
            },
            textStyle: {
              color: "black",
            },
          },
          axisTick: {
            show: false,
          },
          splitLine: {
            show: false,
          },

          data: [],

          inverse: true,
          animationDuration: 300,
          animationDurationUpdate: 300,
          max: 9, // only the largest 3 bars will be displayed
        },
        series: [
          {
            realtimeSort: true,
            name: "粉丝数",
            type: "bar",

            data: [],
            itemStyle: {
              normal: {
                // 线性渐变，前四个参数分别是 x0, y0, x2, y2, 范围从 0 - 1，分别表示右,下,左,上。
                // 例如（0，0，0，1）表示从正上开始向下渐变；如果是（1，0，0，0），则是从正右开始向左渐变。
                // 相当于在图形包围盒中的百分比，如果最后一个参数传 true，则该四个值是绝对的像素位置
                color: new echarts.graphic.LinearGradient(0,0,1,0,
                  [
                    {
                      offset: 0,
                      color: "#ff7c00", //指0%处的颜色
                    },
                    {
                      offset: 1,
                      color: "#ffbe00", //指100%处的颜色
                    },
                  ],
                  false
                ),
              },
            },
          },
          {
            realtimeSort: false,
            name: "转发数",
            type: "bar",

            data: [],
            animationDurationUpdate: 300,
            animationEasingUpdate: "",
            itemStyle: {
              normal: {
                // 线性渐变，前四个参数分别是 x0, y0, x2, y2, 范围从 0 - 1，分别表示右,下,左,上。
                // 例如（0，0，0，1）表示从正上开始向下渐变；如果是（1，0，0，0），则是从正右开始向左渐变。
                // 相当于在图形包围盒中的百分比，如果最后一个参数传 true，则该四个值是绝对的像素位置
                color: new echarts.graphic.LinearGradient(0,0,1,0,
                  [
                    {
                      offset: 0,
                      color: "#0065f1", //指0%处的颜色
                    },
                    {
                      offset: 1,
                      color: "#00c8ff", //指100%处的颜色
                    },
                  ],
                  false
                ),
              },
            },
          },
        ],

        animationDuration: 3000,
        animationDurationUpdate: 3000,
        animationEasing: "linear",
        animationEasingUpdate: "linear",
      },
    };
  },
  mounted() {
    // 图表初始化
    this.myChart = echarts.init(document.getElementById("mychart"));
    // 数据初始化
    this.setData(window.parent.getInfluentialBloggers());
    //第一次执行，
    setInterval(() => {
      this.setData(window.parent.getInfluentialBloggers());
    }, 300000);
    setInterval(() => {
      this.pageUpdate();
    }, 3000);

    this.option && this.myChart.setOption(this.option);
  },
  methods: {
    // 数据刷新
    pageUpdate() {
      this.dynamicSortZZTOption.yAxis.data = this.ydata;
      // console.log(this.dynamicSortZZTOption.series[0].data);
      this.dynamicSortZZTOption.series[0].data = this.sortData1;
      this.dynamicSortZZTOption.series[1].data = this.sortData2;
      this.myChart.setOption(this.dynamicSortZZTOption);
      //随着屏幕大小调节图表
      window.addEventListener("resize", () => {
        this.myChart.resize();
      });
    },
    // 根据不同的屏幕宽度换算字体大小
    transformFontSize(fontsize) {
      // 获取屏幕宽度
      const width = document.body.clientWidth;
      const ratio = width / 792;
      console.log(width);
      // 取下整
      return parseInt(fontsize * ratio);
    },

     setData(listData) {
      this.ydata  = listData.authorList;   //此句因人而异，不同组件接口返回的数据格式不同，需要按返回数据格式赋值给vue的list，请在thingjs中打印或者通过postman确认接口返回的数据格式
      this.sortData1 = listData.repostsCountList;
      var n = listData.followersCountList.length
      for(var i = 0;i<n;i++){
        listData.followersCountList[i] = Math.round(Math.log(listData.followersCountList))
      }
      this.sortData2 = listData.followersCountList;
    },
    setRem() {
      const baseSize = 18;
      const containerDocElm = document.getElementById("ipcontainer");
      const titleDoc = document.getElementById("iptitle");
      // 当前页面宽度相对于 1280宽的缩放比例，可根据自己需要修改。
      // const scale = document.body.clientWidth / 1280;
      const scale = containerDocElm.clientWidth / 1280;
      // 设置页面根节点字体大小（“Math.min(scale, 2)” 指最高放大比例为2，可根据实际业务需求调整）
      titleDoc.style.fontSize = baseSize * Math.min(scale, 2) + "px";
    },
  },
};
</script>

<style>
#mychart {
  width: 100%;
  height: 500px;
}
.iptitle {
  width: 100%;
  height: 10vh;
  /* line-height: 30Px; */
  font-size: 16px;
  font-family: "Microsoft YaHei";

  line-height: 10vh;

  text-align: left;
  text-indent: 1em;
  font-weight: bold;
  color: #ffffff;

  background: url("../../../assets/top.png") no-repeat;
  background-size: cover;
  background-size: 100% 100%;

  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.iptitle .content {
  font-size: 18px !important;
}
.iptitle div {
  display: inline;
}

.echart {
  background: url("../../../assets/bottom.png") no-repeat;
  background-size: cover;
  background-size: 100% 100%;
}
</style>
